.nav {
  text-transform: uppercase;

  &__list {
    display: flex;
    justify-content: space-between;
    list-style: none;
  }

  &__item {
    padding: 8px 24px;
    border-radius: 8px;

    &--active {
      background-color: rgba($header-link-color, 0.1);
    }
  }

  &__link {
    color: $header-link-color;
    @include hover(color, #{$header-link-hover-color});
  }
}
