.person {
  width: 240px;
  text-align: center;

  &__photo {
    width: 153px;
    height: 153px;
    border-radius: 50%;
    margin-bottom: 28px;
  }

  &__name {
    font-size: 24px;
    line-height: 1.25;
    color: $highlight-color;
    margin-bottom: 4px;
  }

  &__position {
    font-size: 14px;
    line-height: 1.93;
    color: #9fadc2;
    margin-bottom: 13px;
  }
}
